import React from 'react';
import PropTypes from 'prop-types';

function Article({
  title,
  text,
  data,
}) {
  return (
    <article>
      <h1>{title}</h1>
      <span>{data}</span>
      <p>{text}</p>
    </article>
  );
}

Article.propTypes = {
  title: PropTypes.string.isRequired,
  text: PropTypes.string.isRequired,
  data: PropTypes.string.isRequired,
};

export default Article;
